"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";

export default function LoginForm() {
  const router = useRouter();

  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });

  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const validate = () => {
    const newErrors = {};

    if (!formData.email.trim()) {
      newErrors.email = "Email is required";
    }

    if (!formData.password.trim()) {
      newErrors.password = "Password is required";
    }

    return newErrors;
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const validationErrors = validate();

    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors);
      return;
    }

    setErrors({});
    console.log("Login data:", formData);

    // Later:
    // router.push("/products");
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-50 to-slate-100 px-4">
      <form
        onSubmit={handleSubmit}
        className="w-full max-w-sm bg-white p-8 rounded-2xl shadow-sm border border-slate-200"
      >
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-semibold text-slate-900 text-center mb-2">
            Welcome Back
          </h1>
          <p className="text-center text-slate-500 text-sm">
            Login to continue shopping
          </p>
        </div>

        {/* Email */}
        <div className="mb-5">
          <input
            type="email"
            name="email"
            placeholder="Email"
            value={formData.email}
            onChange={handleChange}
            className={`w-full px-4 py-2.5 text-slate-900 placeholder-slate-400 border rounded-lg transition-all focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
              errors.email
                ? "border-red-300 bg-red-50"
                : "border-slate-200 bg-slate-50"
            }`}
          />
          {errors.email && (
            <p className="text-red-500 text-xs mt-1.5">{errors.email}</p>
          )}
        </div>

        {/* Password */}
        <div className="mb-6">
          <input
            type="password"
            name="password"
            placeholder="Password"
            value={formData.password}
            onChange={handleChange}
            className={`w-full px-4 py-2.5 text-slate-900 placeholder-slate-400 border rounded-lg transition-all focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent ${
              errors.password
                ? "border-red-300 bg-red-50"
                : "border-slate-200 bg-slate-50"
            }`}
          />
          {errors.password && (
            <p className="text-red-500 text-xs mt-1.5">
              {errors.password}
            </p>
          )}
        </div>

        {/* Login Button */}
        <button
          type="submit"
          className="w-full cursor-pointer bg-blue-600 hover:bg-blue-700 text-white py-2.5 rounded-lg font-medium transition-colors duration-200"
        >
          Login
        </button>

        {/* Footer */}
        <p className="text-center text-sm text-slate-600 mt-6">
          Donâ€™t have an account?
          <button
            type="button"
            onClick={() => router.push("/auth/signup")}
            className="text-blue-600 cursor-pointer font-medium hover:text-blue-700 ml-1 transition-colors duration-200"
          >
            Sign up
          </button>
        </p>
      </form>
    </div>
  );
}
